import React from "react"
import { Link } from "gatsby"
import { formatName } from "../js/scripts"


export default function Nav({ data }) {
  return (
    <nav className="navbar">
      <Link to="/">Home</Link>
      {
        data.allDirectory.edges.map(edge => {
          const name = formatName(edge.node.name)
          const id = edge.node.id
          const path = `/curriculum/${edge.node.relativePath}`
          return <Link to={path} key={id}>{name}</Link>
        })
      }
    </nav>
  )
}


