import React from "react"
import Nav from "./Nav"
import { graphql, useStaticQuery } from "gatsby"

const Header = () => {
    const data = useStaticQuery(
        graphql`
        query navQuery {
            allDirectory(
              filter: {sourceInstanceName: {eq: "curriculum"}, relativeDirectory: {eq: ""}}
            ) {
              edges {
                node {
                  id
                  name
                  relativePath
                  relativeDirectory
                  sourceInstanceName
                }
              }
            }
          }
        `
    )
    return (
        <header>
            <Nav data={data}/>
        </header>
    )
}

export default Header